@import url(https://fonts.googleapis.com/css2?family=Langar&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);

body > #root > div {
  height: 100vh;
}

html, body {
  padding: 0;
  margin: 0;
}

body { 
  background-color: #27AAE2;
}
/* container
  header
  image
  btn
  27AAE2
*/

.landing-container {
  background-color: #27AAE2;
  width: 100%;
  height: 100vw;
  color: white;
}

.landing-header {
  margin: 0;
  text-align: center;
  padding-top: 25px;
  font-family: 'Langar', cursive;
  letter-spacing: 3px;
  font-size: 3em;
}

.landing-container p {
  text-align: center;
  font-size: 1.5em;
  font-family: 'Roboto', sans-serif;
}

.landing-image {
  display: block;
  margin-left: auto;
  margin-right: auto;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
}

.landing-btn {
  text-decoration: none;
  background-color: white;
  border-radius: 5px;
  border: 1px solid gray;
  margin-left: 2px;
  margin-right: 2px;
}

.landing-btn:active {
  background-color: gray;
  color: white;
  border: 1px solid white;
}
.adoption-container {
  display: flex;
  flex-direction: column;
  font-family: 'Roboto', sans-serif;
}

.adoption-container h1, h2, p {
  text-align: center;
  color: white;
}

.adoption-container p {
  margin: 0 auto;
}

.adoption-peopleList {
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.adoption-form {
  margin: 0 auto;
  color: white;
}
.pet-container {
  display: flex;
  flex-direction: column;
  color: white;
  font-family: 'Roboto', sans-serif;
}

.animal-container {
  display: flex;
  flex-direction: column;
  padding: 20px;
  text-align: center;
}

.animal-image {
  border-radius: 5px;
}

.animal-card {
  border-left: 1px solid black;
  border-right: 1px solid black;
  border-bottom: 1px solid black;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  margin-top: 3%;
}

.pet-container p, h3 {
  margin: 0;
}

@media only screen and (min-width: 768px) {
  .animal-container {
    width: 50%;
    margin: 0 auto;
  }
}
.people-container {
  color: white;
}
